
const nullChecks = {
    "MON": { title: "", value: "MON", checked: false },
    "TUE": { title: "", value: "TUE", checked: false },
    "WED": { title: "", value: "WED", checked: false },
    "THU": { title: "", value: "THU", checked: false },
    "FRI": { title: "", value: "FRI", checked: false },
    "SAT": { title: "", value: "SAT", checked: false },
    "SUN": { title: "", value: "SUN", checked: false },
};

const checksReducer = (state = nullChecks, action) => {
    var _state = JSON.parse( JSON.stringify(state) );
    switch( action.type ) {
        case 'Checks/reset':
            return nullChecks;

        case 'Checks/check':
            _state[action.value].checked = true;
            return _state;
        
        case 'Checks/uncheck':
            _state[action.value].checked = false;
            return _state;
        
        case 'Checks/set-init':
            action.value.forEach((value) => {
                _state[value].checked = true;
            })
            return _state;
            
        default:
            return state;
    }
}

export default checksReducer;